<template>
    <div class="bill-pack-wrapper" v-if="load">
        <div class="open-head">
            <div :class="'not-look '+layout" v-if="is_new_user">
                <p>抱歉，2018年你还没有使用过叮咚买菜</p>
                <p>无法查看账单</p>
            </div>
            <div class="can-look" v-if="!is_new_user">
                <p>你的2019开运红包是</p>
            </div>
        </div>
        <div class="open-no" v-if="!is_get_reward" @click="eventOpenCoupon">
            <div class="open-pack active" v-if="load"></div>
        </div>
        <div class="open-had" v-if="is_get_reward">
            <div class="coupon-title" v-if="is_new_user">恭喜你，获得了</div>
            <div class="coupon-one" v-if="!new_user_coupon.length">
                <div class="coupon-one-left">
                    <div class="one-left-price">
                        <span>￥</span>
                        <strong>{{coupon.money}}</strong>
                    </div>
                    <div class="one-left-desc">满{{coupon.pay_min}}元使用</div>
                </div>
                <div class="coupon-one-right">
                    <p class="one-right-name">{{coupon.name}}</p>
                    <p class="one-right-desc">{{coupon.description}}</p>
                    <p class="one-right-time">{{coupon.valid_days}}天后过期</p>
                </div>
            </div>
            <div class="coupon-list" v-if="!!new_user_coupon.length">
                <pack-coupon :coupon="coupon"/>
                <div class="list-part-new">
                    <p class="list-new-title">还有38元新人红包</p>
                    <pack-coupon v-for="(new_coupon, i) in new_user_coupon" :key="i" :coupon="new_coupon"/>
                </div>
            </div>
            <div class="coupon-bottom">
                <p class="coupon-mobile" v-if="!!phone">已放入账户 {{phone}}</p>
                <div class="coupon-bottom-info">
                    <div class="info-btn" v-if="is_new_user" @click="eventCouponUse({cid:'mybill', aid:'use'})">立即使用</div>
                    <p class="info-font" v-if="!is_new_user">{{type}}</p>
                </div>
            </div>
        </div>

        <div class="foot-btn" v-if="!is_new_user">
            <div class="btn-again" @click="eventLookAgain">再看一遍</div>
            <div class="btn-share" @click="eventShare" v-if="platform.isApp">分享给好友</div>
        </div>

        <div :class="'foot-logo '+layout" v-if="is_new_user">
            <img :src="img_logo" alt=""/>
        </div>
    </div>
</template>

<script>
    import share from '@/mixins/share'
    import events from '@/mixins/events'
    import API from '@/server/api'
    import {platform} from '@/utils/platform'
    import {IMG_BILL_PACK_LOGO} from '@/const/images'
    import PackCoupon from '@/components/bill/PackCoupon'
    import {pageUrl} from '@/utils/tools'
    import {local} from '@/utils/storage'

    export default {
        name: 'bill',
        components: {
            PackCoupon
        },
        mixins: [share, events],
        data() {
            return {
                is_new_user: 0,     //是否新用户 1是新用户 0 不是
                is_get_reward: 0,   //是否已领奖 1已领取  0 未领取
                phone: '',
                coupon: null,
                new_user_coupon: [],
                type: '',

                img_logo: IMG_BILL_PACK_LOGO,
                layout: '',
                load: false,
                platform
            }
        },
        created() {
            this.requestCouponInfo();
        },
        methods: {
            async requestCouponInfo() {
                const {data} = await this.$http.get(API.userBill('getUserCouponInfo'));
                if (data) {
                    const {is_new_user, is_get_reward} = data;
                    if(!is_new_user && !is_get_reward){
                        this.requestReward();
                    }else{
                        this.updateData(data);
                    }
                }
            },
            async requestReward(){
                const {data} = await this.$http.get(API.userBill('reward'));
                if(data){
                    this.log({
                        cid: 'mybill',
                        aid: 'get_redpacket'
                    });
                    this.updateData(data);
                }
            },
            updateData(data){
                let {is_new_user, is_get_reward, phone, coupon, new_user_coupon, type} = data;
                this.is_new_user = is_new_user;
                this.is_get_reward = is_get_reward;
                this.phone = phone;
                this.coupon = coupon.length ? coupon[0] : null;
                this.new_user_coupon = new_user_coupon;
                this.layout = new_user_coupon.length ? 'lay-0' : '';
                this.type = type;
                this.load = true;

                //新用户更新分享link
                let {id} = local.load('USER_INFO');
                this.updateShare({link: pageUrl('/bill', {friend_id: is_new_user?'': id})})
            },
            eventLookAgain() {
                this.$router.push({path:'/bill/detail'})
            },
            eventShare() {
                this.log({
                   cid: 'mybill',
                   aid: 'share_last'
                });
                this.appShare();
            },
            eventOpenCoupon() {
                this.log({
                   cid: 'mybill',
                   aid: 'try_redpacket'
                });
                if(this.$store.state.login.user_login){
                    this.requestReward();
                }else{
                    this.$store.state.login.isShowLogin = true;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .bill-pack-wrapper{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        overflow-y: auto;
        overflow-scrolling: auto;
        background-image: url('https://img.ddimg.mobi/assets/h5/bill/bill-bg.png');
    }
    .open-head {
        text-align: center;
        .not-look {
            padding: 90px 0 70px 0;
            &.lay-0 {
                padding: 40px 0 20px 0;
            }
            p {
                font-size: 30px;
                line-height: 60px;
                color: $color_3;
            }
        }
        .can-look {
            padding: 90px 0 70px 0;
            p {
                font-size: 34px;
                color: $color_3;
            }
        }
    }

    .open-no {
        position: relative;
        width: 474px;
        height: 576px;
        margin: 0 auto;
        background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-big-bg.png');
        background-size: 100% 100%;
        .open-pack {
            position: absolute;
            left: 153px;
            top: 280px;
            width: 168px;
            height: 168px;
            background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-btn-open.png');
            background-size: 100% 100%;
        }
    }

    .open-had {
        position: relative;
        width: 638px;
        margin: 0 auto;
        padding: 25px 0 220px 0;
        background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-bg.png');
        border-radius: 20px;
        .coupon-title {
            font-weight: bold;
            font-size: 32px;
            color: $color_f;
            line-height: 60px;
            text-align: center;
        }
    }

    .coupon-one {
        overflow: hidden;
        width: 580px;
        height: 280px;
        background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-coupon-big-bg.png');
        background-size: 100% 100%;
        margin: 20px auto 0 auto;
        padding: 80px 20px 70px 20px;
        box-sizing: border-box;
        display: flex;
    }

    .coupon-list {
        height: 560px;
        padding: 0 30px;
        overflow-y: scroll;
        overflow-scrolling: touch;
        .list-new-title {
            font-size: 32px;
            line-height: 40px;
            font-weight: bolder;
            color: $color_f;
            text-align: center;
        }
    }

    .coupon-one-left {
        padding-right: 20px;
        width: 35%;
        .one-left-price {
            color: $color_o;
            white-space: nowrap;
            overflow: hidden;
            span {
                font-weight: bold;
                font-size: 40px;
            }
            strong {
                font-size: 110px;
            }
        }
        .one-left-desc {
            font-size: 24px;
            color: $color_9;
            text-align: center;
        }
    }

    .coupon-one-right {
        padding-left: 25px;
        border-left: 2px dotted $color_ba;
        .one-right-name {
            font-size: 40px;
            font-weight: bolder;
            color: $color_o;
            margin-bottom: 10px;
        }
        .one-right-desc, .one-right-time {
            font-size: 24px;
            font-weight: 400;
            color: $color_9;
            line-height: 50px;
        }
    }

    .coupon-bottom {
        position: absolute;
        height: 272px;
        left: 0;
        bottom: 0;
        right: 0;
        background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-bottom-bg.png');
        background-size: 100% 100%;
        text-align: center;
        padding: 50px 0 20px 0;
        box-sizing: border-box;
        .coupon-mobile {
            font-size: 22px;
            font-weight: 400;
            line-height: 66px;
            color: $color_fd;
        }
        .coupon-bottom-info {
            padding-top: 10px;
            .info-btn {
                width: 314px;
                height: 117px;
                line-height: 120px;
                margin: 0 auto;
                background-image: url('https://img.ddimg.mobi/assets/h5/activity-bill-pack-btn-use.png');
                background-size: 100% 100%;
                font-size: 40px;
                font-weight: bolder;
                color: $color_f;
            }
            .info-font {
                font-size: 110px;
                font-weight: 400;
                color: $color_f;
            }
        }
    }

    .foot-btn {
        display: flex;
        justify-content: space-around;
        padding: 0 30px;
        margin-top: 80px;
        .btn-again {
            width: 300px;
            height: 90px;
            line-height: 92px;
            border: 3px solid $color_g1;
            border-radius: 90px;
            font-size: 36px;
            font-weight: 400;
            color: $color_g1;
            text-align: center;
        }
        .btn-share {
            width: 300px;
            height: 90px;
            background: $color_g1;
            border-radius: 90px;
            font-size: 36px;
            font-weight: 400;
            color: $color_f;
            line-height: 92px;
            text-align: center;
        }
    }

    .foot-logo {
        width: 454px;
        height: 112px;
        margin: 80px auto 40px auto;
        &.lay-0{
            margin: 40px auto 40px auto;
        }
        img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    /*动画*/
    .open-pack.active{
        animation: bounce-in .8s ease 0s infinite normal;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(1,1);
        }
        60% {
            transform: scale(1.15, 1.15);
        }
        100% {
            transform: scale(1,1);
        }
    }
</style>
